<template>
  <div>
    <h3>权限列表</h3>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: 'my' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>
        <a>权限列表</a>
      </el-breadcrumb-item>
    </el-breadcrumb>
    <br />
    <!-- 测试  -->
    <el-tag
      :key="tag"
      v-for="(tag,index) in dynamicTags"
      closable
      :disable-transitions="false"
      @close="handleClose(tag)"
      @click="test(index)"
    >{{tag}}</el-tag>
    <el-input
      class="input-new-tag"
      v-if="inputVisible"
      v-model="inputValue"
      ref="saveTagInput"
      size="small"
      @keyup.enter.native="handleInputConfirm"
      @blur="handleInputConfirm"
    ></el-input>
    <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>

    <!-- 搜索框  -->
    <el-input
      v-model="search"
      placeholder="输入关键字搜索"
      class="serch_input"
      prefix-icon="el-icon-search"
    />
    <div class="block">
      <!-- 整体表单 :default-sort="{prop: 'date', order: 'descending'}" 排序 -->
      <el-table
        :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
        style="width: 100%"
        v-loading="loading"
        :default-sort="{prop: 'date', order: 'descending'}"
      >
        <!-- prop="date" sortable 绑定date 进行排序  sortable  -->
        <el-table-column label="日期" width="500" prop="date">
          <template slot-scope="scope">
            <i class="el-icon-time"></i>
            <span style="margin-left: 10px">{{ scope.row.date }}</span>
          </template>
        </el-table-column>
        <el-table-column label="姓名" width="500">
          <template slot-scope="scope">
            <el-popover trigger="hover" placement="top">
              <p>姓名: {{ scope.row.name }}</p>
              <p>住址: {{ scope.row.address }}</p>
              <div slot="reference" class="name-wrapper">
                <el-tag size="medium">{{ scope.row.name }}</el-tag>
              </div>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)" type="primary">修改</el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
        <el-table-column label="权限等级">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.admin}}</span>
          </template>
        </el-table-column>
      </el-table>
      <br />
      <!-- <el-pagination layout="prev, pager, next" :total="50" @current-change="current_change"></el-pagination> -->
    </div>

    <!-- 删除按钮弹窗 -->
    <el-dialog title="提示" :visible.sync="dialogVisible" width="30%">
      <span>确认删除此条数据吗？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="Determine">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 修改数据按钮弹窗表单 -->
    <el-dialog title="修改数据" :visible.sync="dialogVisible2" width="30%">
      <span slot="footer" class="dialog-footer">
        <el-form ref="form" :model="form" label-width="80px" :rules="rules">
          <el-form-item label="日期" prop="date">
            <el-input v-model="form.date"></el-input>
          </el-form-item>
          <!-- <el-form-item label="代号" prop="id">
            <el-input v-model="form.id"></el-input>
          </el-form-item>-->
          <el-form-item label="姓名" prop="name">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="地址" prop="address">
            <el-input v-model="form.address"></el-input>
          </el-form-item>
          <el-form-item label="权限" prop="admin">
            <el-input v-model="form.admin"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">立即创建</el-button>
            <el-button @click="Return">取消</el-button>
          </el-form-item>
        </el-form>
      </span>
    </el-dialog>
    <div onload="init()">
      <div id="container"></div>
      <p>该地图禁址了鼠标拖动、鼠标轮滚缩放、双击放大功能。</p>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      //搜索框
      search: "",
      //加载
      loading: true,
      //删除按钮弹窗
      dialogVisible: false,
      //修改数据按钮弹窗
      dialogVisible2: false,
      //记录下标地址
      index1: "",
      //修改数据表单
      form: {
        date: "",
        name: "",
        address: "",
        admin: "5"
      },
      //修改数据表单验证
      rules: {
        date: [
          {
            required: true,
            message: "请输入日期如：2016-05-02",
            trigger: "blur"
          },
          { min: 8, message: "大于8个字符", trigger: "blur" }
        ],
        name: [
          { required: true, message: "请输入姓名", trigger: "blur" },
          { min: 2, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" }
        ],
        address: [
          { required: true, message: "请输入地址", trigger: "blur" },
          { min: 3, max: 20, message: "长度在 3 到 20 个字符", trigger: "blur" }
        ],
        admin: [
          { required: true, message: "权限", trigger: "blur" },
          { max: 1, message: "一个字符", trigger: "blur" }
        ]
      },
      tableData: [
        {
          date: "2016-05-02",
          name: "王小虎1",
          address: "上海市普陀区金沙江路 1518 弄",
          admin: "2",
          id: "0"
        },
        {
          date: "2016-05-03",
          name: "王小虎2",
          address: "上海市普陀区金沙江路 1518 弄",
          admin: "5",
          id: "1"
        },
        {
          date: "2016-05-05",
          name: "王小虎3",
          address: "上海市普陀区金沙江路 1517 弄",
          admin: "5",
          id: "2"
        },
        {
          date: "2016-05-06",
          name: "王小虎4",
          address: "上海市普陀区金沙江路 1519 弄",
          admin: "5",
          id: "3"
        },
        {
          date: "2016-05-07",
          name: "王小虎5",
          address: "上海市普陀区金沙江路 1516 弄",
          admin: "5",
          id: "4"
        },
        {
          date: "2016-05-08",
          name: "王小虎6",
          address: "上海市普陀区金沙江路 1518 弄",
          admin: "5",
          id: "5"
        },
        {
          date: "2016-05-09",
          name: "王小虎7",
          address: "上海市普陀区金沙江路 1517 弄",
          admin: "5",
          id: "6"
        },
        {
          date: "2016-05-01",
          name: "王小虎8",
          address: "上海市普陀区金沙江路 1519 弄",
          admin: "5",
          id: "7"
        },
        {
          date: "2016-05-10",
          name: "王小虎9",
          address: "上海市普陀区金沙江路 1516 弄",
          admin: "5",
          id: "8"
        },
        {
          date: "2016-05-11",
          name: "王小虎10",
          address: "上海市普陀区金沙江路 1518 弄",
          admin: "5",
          id: "9"
        },
        {
          date: "2016-05-12",
          name: "赵四",
          address: "上海市普陀区金沙江路 1518 弄",
          admin: "5",
          id: "10"
        }
        // {
        //   date: "2016-05-04",
        //   name: "王小虎",
        //   address: "上海市普陀区金沙江路 1517 弄",
        //   admin: "5"
        // },
        // {
        //   date: "2016-05-01",
        //   name: "王小虎",
        //   address: "上海市普陀区金沙江路 1519 弄",
        //   admin: "5"
        // },
        // {
        //   date: "2016-05-03",
        //   name: "王小虎",
        //   address: "上海市普陀区金沙江路 1516 弄",
        //   admin: "5"
        // }
      ],
      dynamicTags: ["标签一", "标签二", "标签三"],
      inputVisible: false,
      inputValue: ""
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init: function() {
      //获取地图显示控件
      var map = new qq.maps.Map(document.getElementById("container"), {
        //加载地图经纬度信息
        center: new qq.maps.LatLng(39.916527, 116.397128),
        zoom: 13, //设置缩放级别
        draggable: false, //设置是否可以拖拽
        scrollwheel: false, //设置是否可以滚动
        disableDoubleClickZoom: true //设置是否可以双击放大
      });
    },
    test(index) {
      console.log(index);
    },

    handleClose(tag) {
      this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
    },

    showInput() {
      this.inputVisible = true;
      // this.$nextTick(_ => {
      //   this.$refs.saveTagInput.$refs.input.focus();
      // });
    },

    handleInputConfirm() {
      let inputValue = this.inputValue;
      console.log(inputValue);
      if (inputValue) {
        this.dynamicTags.push(inputValue);
      }
      this.inputVisible = false;
      this.inputValue = "";
    },
    //监听页码值改变的事件
    current_change(changesize) {
      console.log(changesize);
    },
    //删除按钮弹窗取消点击事件
    cancel() {
      this.dialogVisible = false;
      this.$message.error("取消删除");
    },
    //删除按钮点击确认删除事件
    Determine() {
      this.dialogVisible = false;
      this.$message({
        message: "删除成功",
        type: "success"
      });
      console.log(this.index1);
      this.tableData.splice(this.index1, 1);
    },
    //点击修改数据操作
    handleEdit(index, row) {
      // console.log(index, row);
      this.dialogVisible2 = true;
      this.index1 = row.id;
      console.log(row.id);
      this.form = row;
    },
    //点击删除按钮操作
    handleDelete(index, row) {
      console.log(index, row);
      this.dialogVisible = true;
      this.index1 = row.id;

      console.log(index);
    },
    Return() {
      this.dialogVisible2 = false;
    },
    //点击修改数据表单确认按钮操作
    onSubmit() {
      this.$refs.form.validate(valid => {
        console.log(valid);
        if (!valid) {
          this.$message.error("修改失败，请稍后重试");
        } else {
          this.$message({
            message: "修改成功",
            type: "success"
          });

          //跳转登录后页面
          // this.tableData.push(this.form);
          this.tableData.splice(this.index1, 0, this.form);
          this.tableData.splice(this.index1, 1);
          console.log(this.index1);
          this.$message({
            message: "修改成功",
            type: "success"
          });
          this.dialogVisible2 = false;
        }
      });
    }
  },
  mounted: function() {
    setTimeout(() => {
      this.loading = false;
    }, 400);
  }
};
</script>

<style lang="less" scoped>
.serch_input {
  width: 300px;
  float: right;
  cursor: pointer;
}
#container {
  min-width: 600px;
  min-height: 767px;
}
</style>