<template>
  <div class="login_container">
    <div class="login_box">
      <h2 class="center">登录</h2>
      <el-form
        :rules="rules"
        ref="ruleForm"
        :model="relform"
        label-width="100px"
        class="demo-ruleForm"
      >
        <!-- 用户名 -->
        <el-form-item label="用户名" prop="name">
          <el-input prefix-icon="el-icon-search" v-model="relform.name"></el-input>
        </el-form-item>
        <!-- 密码  -->
        <el-form-item label="密码" prop="password">
          <el-input prefix-icon="el-icon-search" v-model="relform.password" type="password"></el-input>
        </el-form-item>
        <el-form-item>
          <!-- 登录重置  -->
          <div class="btn">
            <el-button type="primary" @click="login">登录</el-button>
            <el-button type="info" @click="resetFrom">重置</el-button>
          </div>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      //表单数据
      relform: {
        name: "admin",
        password: ""
      },

      rules: {
        //验证用户名是否合法
        name: [
          //   { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" }
        ],
        //验证密码是否合法
        password: [
          { min: 8, max: 12, message: "请输入8位以上", trigger: "change" }
        ]
      }
    };
  },
  methods: {
    //点击重置表单
    resetFrom() {
      //   console.log(this);
      this.$refs.ruleForm.resetFields();
    },
    //登录弹框
    login() {
      this.$refs.ruleForm.validate(valid => {
        console.log(valid);
        if (!valid) {
          this.$message.error("登陆失败");
        } else {
          this.$message({
            message: "登陆成功",
            type: "success"
          });
          //跳转登录后页面
          this.$router.push("/home");
        }
      });
    }
  }
};
</script>
<style lang="less" scoped>
.login_container {
  background-color: #2b4b6b;
  background: url(https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1582129896718&di=14aec7f4118ab3d598a6e9bca7f7edd7&imgtype=0&src=http%3A%2F%2Fimages.wondershare.com%2Fimages%2F4k%2Fkwallpaper-9.jpg)
    no-repeat;
  height: 100%;
}
.login_box {
  background-color: white;
  opacity: 0.9;
  width: 450px;
  height: 300px;
  border-radius: 3px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.demo-ruleForm {
  position: absolute;
  bottom: 0;
  margin-left: -40px;
  width: 100%;
  padding: 10px 20px;
  box-sizing: border-box;
}
.btn {
  margin-left: 65px;
}
.center {
  text-align: center;
  color: #606266;
}
</style>